import React from 'react'
import http from 'http'
import { Map } from 'immutable';
import StatusCode from './StatusCode';
import './app-style.css';

const statusCodes = Map(http.STATUS_CODES).sortBy((_, key) => key)
                                          .map((value, key) => <StatusCode key={key} code={key} status={value} />).toList();

const App = () =>
    <div>
        <h1 style={{ textAlign: 'center' }}>Node.js HTTP Status Codes</h1>
        <div id="statusCodes">
            {statusCodes}
        </div>
    </div>

export default App


